         // 下单时间
    nowtime: function () {
        
      var date = new Date();
      //获取当前的年
      var year = date.getFullYear();
      //获取当前的月
      var month = date.getMonth() + 1;
      //获取当前的日
      var day = date.getDate();
      //获取当前的时
      var hh = date.getHours();
      //获取当前的分
      var mm = date.getMinutes();
      mm>10?mm:'0'+mm
      //获取当前的秒
      var ss = date.getSeconds();
      ss>10?ss:'0'+ss
      var rq =
        year + "-" + month + "-" + day + '    ' + hh + ":" + mm + ":" + ss;
        return rq;
      
    },
  },
  created(){
